<template>
  <div>
    <Tutorial />
    <div class="form-container">
      <h1>Distanz zum Baum und Standort</h1>

      <form @submit.prevent="handleSubmit">
        <div class="form-group">
          <label for="steps">Anzahl der Schritte</label>
          <input type="number" id="steps" v-model.number="steps" required min="0" step="1" placeholder="z. B. 3" />
        </div>

        <div class="form-group">
          <button type="submit" class="submit-button">Absenden</button>
        </div>
      </form>

      <div class="form-group">
        <button @click="getLocation" class="location-button">Standort abrufen</button>
      </div>

      <div>
        <p>Uhrzeit: {{ time }} </p>
      </div>

      <div v-if="location">
        <p>Standort: {{ location.latitude }}°, {{ location.longitude }}°</p>
        <p>Abweichung: {{ location.accuracy }} Meter</p>
      </div>

      <div v-if="submitted">
        <p>Die berechnete Distanz zum Baum beträgt: <strong>{{ calculatedDistance }} Meter</strong></p>
      </div>

      <!-- Anzeige der Karte -->
      <div class="map-container" v-if="location">
        <h2>Karte mit Standort</h2>
        <iframe :src="iframeUrl" width="425" height="350" style="border: 1px solid black"></iframe>
        <br />
      </div>
    </div>
  </div>
</template>

<script>
import { Geolocation } from '@capacitor/geolocation';
import Tutorial from '@/components/FirstTutorial.vue';

export default {
  name: 'DistanceForm',
  components: {
    Tutorial,
  },
  data() {
    return {
      steps: '',
      submitted: false,
      location: null,
      time: null,
      stepLength: parseFloat(localStorage.getItem("derivedStepSize") || localStorage.getItem("manualStepSize") || "0") / 100, 
    };
  },
  computed: {
    calculatedDistance() {
      return (this.steps * this.stepLength).toFixed(2);
    },
    iframeUrl() {
      const { latitude, longitude } = this.location;
      return `https://www.openstreetmap.org/export/embed.html?bbox=${longitude}%2C${latitude}%2C${longitude}%2C${latitude}&layer=mapnik&marker=${latitude}%2C${longitude}`;
    },
  },
  mounted() {
    this.getTimeByIP();
  },
  methods: {
    async getTimeByIP() {
      try {
        const response = await fetch("http://worldtimeapi.org/api/ip");
        const data = await response.json();
        this.time = data.datetime;
      } catch (error) {
        setTimeout(this.getTimeByIP, 5000);
      }
    },
    async handleSubmit() {
      this.submitted = true;
      const data = {
        steps: this.steps,
        distance: this.calculatedDistance,
        time: this.time,
        location: this.location ? {
          latitude: this.location.latitude,
          longitude: this.location.longitude,
          accuracy: this.location.accuracy,
        } : null,
      };
      const jsonData = JSON.stringify(data, null, 2);
      const blob = new Blob([jsonData], { type: 'application/json' });
      const a = document.createElement('a');
      a.href = URL.createObjectURL(blob);
      a.download = 'data.json';
      a.click();
      URL.revokeObjectURL(a.href);
    },
    async getLocation() {
      try {
        const position = await Geolocation.getCurrentPosition({ enableHighAccuracy: true });
        if (position.coords) {
          this.location = position.coords;
        }
      } catch (error) {
        console.log("Fehler beim Abrufen des Standorts");
      }
    },
  },
};
</script>

<style scoped>
@import './style.css';
</style>
